import React from 'react';
import * as LucideIcons from 'lucide-react';
import { HelpCircle } from 'lucide-react';

function Icon({
    name,
    size = 24,
    color = "currentColor",
    className = "",
    strokeWidth = 2,
    title,
    ...props
}) {
    const IconComponent = LucideIcons?.[name];

    const accessibilityProps = title
        ? { "aria-label": title, title, role: "img" }
        : { "aria-hidden": true, role: "img" };

    if (!IconComponent) {
        return <HelpCircle size={size} color="gray" strokeWidth={strokeWidth} className={className} {...props} {...accessibilityProps} />;
    }

    return <IconComponent
        size={size}
        color={color}
        strokeWidth={strokeWidth}
        className={className}
        {...props}
        {...accessibilityProps}
    />;
}
export default Icon;